<template>
    <div class="book">
		
		<Login @booklist='findAll'></Login>
		
		<div class="container">
		  <div class="row">
		    <div class="col">
				<!-- 各期列表 -->
				<div id="user-jiazaiTS" style="color: #DC3545;"></div>
				<div id="user-shuchu">
					<!-- 查询列表 -->
					<div class="chaxun__list">
						<ul class="chaxun_content" v-for="item in kecheng">
							<li @click="getbook1(item.key)">{{ item.name }}</li>
						</ul>
					</div>
				</div>
			</div>
		    <div class="col">
				<!-- 一期列表 -->
				<div class="music-player__list">
					<ul class="music__list_content" v-for="item in books">
						<li class="music__list__item " @click="getbook2(item.key)">{{ item.name }}</li>
					</ul>
				</div>
			</div>
		  </div>	
		</div>
		
		<div id="wenzi"></div>

       

        

    </div>
</template>

<script>

    import Mp3 from '@/components/Mp3.vue'
	import Login from '@/components/Login.vue'

    var Bmob = require("@/zi____yuan/Bmob-2.2.5.min.js");
    Bmob.initialize("d120dedc62382103", "201068");

    export default {
        name: 'Book',
        components: {
            Login
        },
        data() {

            return {
				kecheng: [],
                message1: '1',
                message2: '2',
                username: '',
                password: '',
                ts: '',
                wx: '点击复制',
				isshow: true,
                books: '',

            }
        },

        methods: {
			
			findAll(books) {
				console.log('父组件中的事件')
				console.log(books)
				this.kecheng = books
			},
			
			getbook1(mima) {
				 const query = Bmob.Query('book');
				 				 
				  query.get(mima).then(res => {
				    console.log(res.book)
				    console.log('成功')
				    this.books = res.book
				   
				 			  
				  }).catch(err => {
				    console.log(err)
				  })
			},
			
			//生成1期列表
			
			getbook2(mima) {
				const query = Bmob.Query('wenzi');
								 
				 query.get(mima).then(res => {
				   // console.log(res)
				   console.log('成功')
				   // 获取某列的数据
				   var wenzi1 = document.getElementById('wenzi');
				   wenzi1.innerHTML = res.wenzi
				   
							  
				 }).catch(err => {
				   console.log(err)
				 })
			},
			
			
			
        
		}	

    }

</script>

