<template>
    <div class="Login">


        <div class="container">
            <div v-show="isshow">
                <div class="form-group row">
                    <label for="colFormLabel" class="col-2 col-form-label">用户名</label>
                    <div class="col-10">
                        <input type="text" class="form-control" id="colFormLabel" placeholder="请输入" v-model="username">
                    </div>
                </div>

                <div class="form-group row">
                    <label for="inputPassword5" class="col-2 col-form-label">密码</label>
                    <div class="col-10">
                        <input type="password" id="inputPassword5" class="form-control"
                            aria-describedby="passwordHelpBlock" v-model="password">
                    </div>
                </div>

                <div class="btn">
                    <button class="btn btn-success" type="default" @click="denglu">登录</button>
                    <small>第一次登录时，输入用户名和密码后，点击【登录】</small>
                </div>


                <div class="yjbtn">
                    <button class="btn btn-warning" type="default" @click="yjdl">一键登录</button>
                    <small>再次登录时，不用输入用户名和密码，直接点击【一键登录】</small>
                </div>
            </div>

            <div class="ts-view text-center">
                <p class="ts" style="color: red;">{{ts}}</p>
            </div>
			

        </div>

        

    </div>
</template>

<script>


    var Bmob = require("@/zi____yuan/Bmob-2.2.5.min.js");
    Bmob.initialize("d120dedc62382103", "201068");

    export default {
        name: 'Login',
        
        data() {

            return {
                message1: '1',
                message2: '2',
                username: '',
                password: '',
                ts: '',
                wx: '点击复制',
				isshow: true,
                mp3s: [],
				books: [],

            }
        },

        methods: {
            denglu() {


                let username = this.username;
                let password = this.password;


                Bmob.User.login(username, password).then(res => {
                    console.log(res)

                    let current = Bmob.User.current()
                    console.log(current.objectId)
                    const query = Bmob.Query('_User');

                    query.get(current.objectId).then(res => {
                        // console.log(res)
                        console.log('成功')

                        console.log("登录成功")
                        this.ts = username + ' ,已登录成功'
                        this.username = ''
                        this.password = ''
						this.isshow = false
						this.mp3s = res.unimp3Qx
						this.books = res.unibookQx
						this.$emit('aa',res.unimp3Qx)
						this.$emit('booklist',res.unibookQx)


                    }).catch(err => {
                        console.log(err)
                    })
                }).catch(err => {
                    console.log(err)
                });

            },

            yjdl() {
                let current = Bmob.User.current()
                this.ts = '查询中，稍等一下下！'

                const query = Bmob.Query('_User');

                query.get(current.objectId).then(res => {
                    // console.log(res)
                    console.log('登录成功')
                    // console.log(res.username)

                    this.ts = res.username + ' ,已登录成功'
					this.isshow = false
					this.mp3s = res.unimp3Qx
					this.$emit('aa',res.unimp3Qx)
					this.$emit('booklist',res.unibookQx)

                }).catch(err => {
                    console.log(err.error)
                });
				
            },
			

			
			

        },
        created() {
            this.yjdl()
        }

    }

</script>

